@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* ===== Google Fonts ===== */

/* ===== Base Style ===== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #f3f4e6;
}

html {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #0e7467;
  font-family: "Montserrat", sans-serif;
  margin: 0;
}

h1,
h2 {
  font-size: 2rem;
  font-weight: 500;
}

p {
  color: #3b3f4a;
  opacity: 75%;
  font-size: 1rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

ul {
  list-style: none;
  margin: 0;
}

a {
  color: unset;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
}

a:hover {
  color: unset;
}

section,
footer {
  padding: 60px 0px;
}

::-webkit-scrollbar {
  width: 10px;
  background: #9c2052;
}

::-webkit-scrollbar-thumb {
  border-radius: 2em;
  background: #0e7467;
}

.header {
  width: 100%;
  height: 70px;
  line-height: 70px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.nav-list {
  display: flex;
  align-items: center;
  grid-column-gap: 2.7rem;
  -webkit-column-gap: 2.7rem;
          column-gap: 2.7rem;
}

.logo h5 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #0e7467;
}

.nav-item a {
  color: #9c2052;
  transition: 0.3s;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.3rem;
  cursor: pointer;
}

.nav-item a:hover {
  color: #0e7467;
}

.btn {
  background: #9c2052;
  color: #f3f4e6;
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 0.9rem;
  padding: 7px 25px;
  font-weight: 700;
}

.btn:hover {
  background: #0e7467;
  color: #f3f4e6;
}

.mobile-menu {
  display: none;
}

.mobile-menu i {
  color: #9c2052;
  font-size: 1.3rem;
  cursor: pointer;
}

.header-shrink {
  background: #e6e8c9;
  box-shadow: 2px 2px 4px -2px #9c2052;
}

/* ===== Responsive ===== */
@media only screen and (max-width: 992px) {
  .nav-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #e6e8c985;
    z-index: 888888;
    display: none;
  }

  .nav-list {
    position: absolute;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    background: #e6e8c9;
    z-index: 999999;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
  }

  .menu-active {
    display: block;
  }

  .mobile-menu {
    display: block;
  }

  .btn {
    display: none;
  }

  .header {
    height: 50px;
    line-height: 50px;
  }
}

.cv-btn {
  background: #9c2052;
  color: #f3f4e6;
}

.cv-btn a {
  text-decoration: none;
  font-weight: 500;
}

.contact-btn a {
  text-decoration: none;
}

.hero-content h5 {
  font-weight: 300;
}

.hero-title {
  color: #3b3f4a;
}

.hero-title span {
  color: #0e7467;
}

.hero-run {
  color: blue;
}

.hero-img {
  width: 400px;
  height: 400px;
  margin-left: auto;
}

.hero-section {
  padding-top: 150px;
}

.hero-content p {
  line-height: 30px;
  font-weight: 400;
}

/* ===== Responsive ===== */
@media only screen and (max-width: 992px) {
  .hero-img {
    width: 300px;
    height: 300px;
  }

  .hero-content p {
    font-size: 0.8rem;
  }

  .hero-btns button {
    display: block;
    padding: 5px 20px;
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 576px) {
  .hero-img {
    margin: auto;
  }

  .hero-content {
    margin-bottom: 50px;
  }

  .hero-title {
    font-size: 1.5rem;
  }
}

.about-btns {
  background: #e6e8c9;
  border-radius: 10px;
}

.about-btn {
  border: none;
  outline: none;
  width: 100%;
  padding: 20px;
  color: #9c2052;
  font-weight: 600;
  background: transparent;
  border-bottom: 1px solid #0e7467;
  border-radius: 5px 5px 0px 0px;
}

.about-btn:last-child {
  border-bottom: 0;
  border-radius: 0px 0px 5px 5px;
}

.active {
  background: #9c2052;
  color: #e6e8c9;
}

.about-content-wrapper {
  background: #e6e8c9;
  padding: 20px;
  border-radius: 10px;
}

.about-content h2 {
  margin-bottom: 15px;
}

.about-content p {
  line-height: 30px;
  font-weight: 400;
}

.social-links span a i {
  color: #9c2052;
}

.social-links span {
  margin-top: 10px;
  margin-right: 15px;
}

.text-justify {
  text-align: justify;
  font-size: 18px;
}

/* ===== Responsive ===== */
@media only screen and (max-width: 992px) {
  .about-btn {
    padding: 10px 25px;
    font-size: 0.8rem;
  }

  .about-btns {
    margin-bottom: 30px;
  }

  .about-content p {
    font-size: 0.9rem;
    line-height: 28px;
  }
}

.edu-content {
  background: #e6e8c9;
  border-radius: 10px;
}

.edu-logo img {
  width: 180px;
}

.edu-logo2 img {
  width: 110px;
}

.edu-title h5 {
  color: #54514a;
  font-weight: bold;
}

.edu-title h6 {
  color: grey;
}

.edu-title {
  margin-bottom: 15px;
}

.skills_wrapper {
  background: #e6e8c9;
  padding: 20px;
  border-radius: 10px;
}

.skill_bar {
  height: 5px;
  width: 100%;
  position: relative;
  background: whitesmoke;
  border-radius: 50px;
  z-index: 999;
}

.skill_bar-percentage {
  background: #9c2052;
  height: 5px;
  position: absolute;
  z-index: 1000;
  top: 0;
  left: 0;
  border-radius: 50px;
}

.skill_title span {
  color: #0e7467;
}

.skill_title {
  margin-bottom: 10px;
}

.skills-img img {
  width: 100%;
  border-radius: 10px;
}

.single_experience {
  background: #e6e8c9;
  padding: 10px;
  border-radius: 10px;
  border-bottom: 1px solid #0e7467;
}

.experience_item {
  width: 47%;
  min-height: 13rem;
  margin-bottom: 15px;
}

.experience_title span {
  font-weight: 300 !important;
  color: #3b3f4a;
  opacity: 80% !important;
  font-size: 0.8rem;
}

.experience_comdiv {
  color: #3b3f4a;
  font-weight: 600;
  margin-bottom: 5px;
}

.red {
  color: #9c2052;
}

.experience_year {
  color: #0e7467;
  font-weight: bold;
  margin-bottom: 10px;
}

.portfolio-top span {
  color: #9c2052;
}

.portfolio-top h6 {
  text-align: center;
  font-size: 1.3rem;
  margin-bottom: 3rem;
}

.portfolio-top h1 {
  color: #0e7467;
  text-align: center;
  margin-bottom: 1rem;
  font-size: 3rem;
}

.portfolio-top h4 {
  text-align: center;
  color: #9c2052;
  margin-bottom: 2rem;
  font-weight: BOLD;
}

.portfolio-top img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.portfolio-img img {
  width: 100%;
  border-radius: 10px;
}

.portfolio-card {
  margin-bottom: 1rem;
}

.portfolio-mid h4 {
  text-align: center;
  color: #9c2052;
  margin-bottom: 2rem;
  font-weight: BOLD;
}

.portfolio-mid img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  margin-top: 3rem;
}

.footer {
  border-top: 1px solid #e6e8c9;
  padding-top: 20px;
}

.footer p {
  font-weight: 300;
  font-size: 0.9rem;
}

